import { extend } from 'vee-validate'
import { required, email, confirmed, min } from 'vee-validate/dist/rules'

extend("required", {
    ...required,
    message: "Campo Obrigatrio"
});
extend("email", {
    ...email,
    message: "Ex: nome@domnio.com"
});
extend("confirmed", {
    ...confirmed,
    message: "Valor invlido"
});
extend("min", {
    ...min,
    message: "mnimo 8 caracters"
});